import Vue from 'vue';
import Vuex from 'vuex';
import apiFrontCart from '@/assets/js/api/front/apiFrontCart';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    user: {
      token: '',
      uuid: 'e52a7eba-6d5f-485f-9699-084e888b3388',
      apiPath: 'https://course-ec-api.hexschool.io/',
    },
    isLoading: false,
    cartLength: 0,
  },
  mutations: {
    SET_USER(state, payload) {
      state.user.token = payload.token;
      if (payload.uuid) {
        state.user.uuid = payload.uuid;
      }
    },
    SET_LOADING(state, payload) {
      state.isLoading = payload;
    },
    SET_CART_LENGTH(state, payload) {
      state.cartLength = payload;
    },
  },
  actions: {
    setCartLength({ commit }) {
      apiFrontCart
        .get('')
        .then(res => {
          commit('SET_CART_LENGTH', res.data.data.length);
        })
        .catch(err => {
          this.$fire({
            title: `${err}`,
            type: 'error',
            timer: 3000,
          });
        });
    },
  },
  modules: {},
});
