import Vue from 'vue';
import VueRouter from 'vue-router';
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err);
};
Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'guid',
    component: () => import('../views/Front/Guid/Guid.vue'),
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('../views/Login/Login.vue'),
  },
  {
    path: '/front',
    name: 'front',
    redirect: '/front/home',
    component: () => import('../views/Front/Front.vue'),
    children: [
      {
        path: 'home',
        name: 'home',
        component: () => import('../views/Front/Home/Home.vue'),
      },
      {
        path: 'products',
        name: 'products',
        component: () => import('../views/Front/Products/Products.vue'),
      },
      {
        path: 'product/:id?',
        name: 'product',
        component: () => import('../views/Front/Product/Product.vue'),
      },
      {
        path: 'coupon',
        name: 'coupon',
        component: () => import('../views/Front/Coupon/Coupon.vue'),
      },
      {
        path: 'work',
        name: 'work',
        component: () => import('../views/Front/Work/Work.vue'),
      },
      {
        path: '',
        name: 'shopping',
        redirect: 'shoppingstep1',
        component: () => import('../views/Front/Shopping/Shopping.vue'),
        children: [
          {
            path: 'shoppingstep1',
            name: 'shoppingstep1',
            component: () =>
              import('../views/Front/Shopping/components/ShoppingStep1.vue'),
          },
          {
            path: 'shoppingstep2',
            name: 'shoppingstep2',
            component: () =>
              import('../views/Front/Shopping/components/ShoppingStep2.vue'),
          },
          {
            path: 'shoppingstep3',
            name: 'shoppingstep3',
            component: () =>
              import('../views/Front/Shopping/components/ShoppingStep3.vue'),
          },
        ],
      },
    ],
  },
  {
    path: '/back',
    name: 'back',
    redirect: '/back/dashboard',
    component: () => import('../views/Back/Back.vue'),
    children: [
      {
        path: 'dashboard',
        name: 'dashboard',
        component: () => import('../views/Back/Dashboard/Dashboard.vue'),
      },
      {
        path: 'backcoupon',
        name: 'backcoupon',
        component: () => import('../views/Back/Coupon/Coupon.vue'),
        meta: { requiresAuth: true },
      },
      {
        path: 'product',
        name: 'backproduct',
        component: () => import('../views/Back/Product/Product.vue'),
        meta: { requiresAuth: true },
      },
      {
        path: 'order',
        name: 'order',
        component: () => import('../views/Back/Order/Order.vue'),
        meta: { requiresAuth: true },
      },
      {
        path: 'storage',
        name: 'storage',
        component: () => import('../views/Back/Storage/Storage.vue'),
        meta: { requiresAuth: true },
      },
      {
        path: 'backguide',
        name: 'backguide',
        component: () => import('../views/Back/BackGuide/BackGuide.vue'),
        meta: { requiresAuth: true },
      },
    ],
  },
];

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes,
});

export default router;
