export default {
  inserted: function(el, binding) {
    el.onclick = function() {
      const anchor = document.querySelector(`#${binding.value}`);
      let documentTop = document.documentElement.scrollTop;
      if (documentTop < anchor.offsetTop) {
        const timeHandler = setInterval(() => {
          documentTop += 10;
          document.documentElement.scrollTop = documentTop;
          if (documentTop >= anchor.offsetTop) {
            clearInterval(timeHandler);
          }
        }, 10);
      }
    };
  },
};
